<template>
	<box-item title="种植业面积">
		<div class="module-wrap">
			<div class="total-info-wrap">
				<p class="total-info">
					<b class="value">
						{{ maxDecimalPlaces({value: detailAreaList.reduce((total, item) => total + item.value, 0)}) }}
					</b>
					万亩
				</p>
			</div>
			<ul class="detail-info">
				<li class="info-item" v-for="(item, index) in detailAreaList" :key="index">
					<img class="info-pic" :src="item.icon" :alt="item.name" />
					<p class="info-name">{{ item.name }}</p>
					<p class="info-value">{{ item.value }}万亩</p>
				</li>
			</ul>
		</div>
	</box-item>
</template>

<script>
import BoxItem from '@/components/common/BoxItem';
import {maxDecimalPlaces} from '@/common/fn';
export default {
	name: 'PlantingArea',
	props: {
		detailAreaList: {
			type: Array,
			default() {
				return [];
			}
		}
	},
	data() {
		return {};
	},
	created() {},
	mounted() {},
	methods: {
		maxDecimalPlaces
	},
	components: {
		BoxItem
	}
};
</script>

<style scoped lang="less">
.module-wrap {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
}
.total-info-wrap {
	padding: 0 20px;
	margin: 0 auto;
	.total-info {
		font-size: 18px;
		color: #feeb48;
		padding: 5px 0 16px;
		height: 34px;
		line-height: 34px;
		text-align: center;
		border-bottom: 1px solid #07758d;
		.value {
			font-size: 32px;
			margin-right: -5px;
		}
	}
}
.detail-info {
	margin: 10px auto 0;
	padding: 0 20px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	.info-item {
		display: flex;
		flex-direction: column;
		align-items: center;
		.info-pic {
			width: 44px;
			border: 0 none;
		}
		.info-name {
			margin-top: 2px;
			color: #71ccde;
			font-size: 14px;
			height: 24px;
			line-height: 24px;
		}
		.info-value {
			margin-top: 2px;
			font-size: 18px;
			height: 28px;
			line-height: 28px;
			color: #feeb48;
			font-weight: bold;
		}
	}
}

.total-info-wrap,
.detail-info {
	max-width: calc(420px * 1.5);
	width: 100%;
	box-sizing: border-box;
}
/* 32:9超宽屏幕适配 */
@media screen and (min-width: 3000px) {
	.detail-info .info-item .info-value {
		font-size: 20px;
	}
	.total-info-wrap .total-info[data-v-75e49a6e] {
		font-size: 20px;
		padding: 5px 0 10px;
	}
}
</style>



